@import './var.scss';

.new-scan {
  .new-scan-main {
    margin: 0 auto;
    padding-bottom: 100px;
    width: 100%;
    max-width: min(1200px, 90vw);

    .new-scan-section {
      margin-bottom: 40px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    .new-scan-title {
      margin-bottom: 20px;
      padding: 0 10px;
      font-size: 20px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: #ffffff;
      line-height: 28px;
    }
    .table-box {
      border-radius: 20px;
      overflow: hidden;
      background: rgba(47, 47, 65, 1);

      .ant-tabs-nav {
        margin: 0 0 10px 0;
      }
      .ant-tabs-nav-wrap {
        padding: 10px 40px;
        .ant-tabs-ink-bar {
          height: 4px;
          background: transparent;
          &:after {
            content: '';
            position: absolute;
            left: 50%;
            top: 0;
            width: 30px;
            height: 100%;
            background: linear-gradient(90deg, #9482f8 0%, #e64dff 100%);
            border-radius: 2px;
            transform: translateX(-50%);
          }
        }
        .ant-tabs-tab {
          padding: 10px 0;
          font-size: 20px;
          line-height: 28px;
          color: rgba(255, 255, 255, 0.3);
          &.ant-tabs-tab-active {
            .ant-tabs-tab-btn {
              color: #fff;
            }
          }
          & + .ant-tabs-tab {
            margin: 0 0 0 30px;
          }
          .ant-tabs-tab-btn {
            &:active {
              color: #fff;
            }
          }
        }
      }
    }
  }

  .token-wrap {
    padding: 30px 0;
    display: flex;
    color: #fff;
    .token-select {
      display: flex;
      align-items: center;
      // justify-content: center;
      min-width: 160px;
      width: auto;
      margin-right: 20px;
      padding-right: 10px;
      height: 40px;
      background: #2f2f41;
      border-radius: 10px;
      cursor: pointer;
      user-select: none;
      &:last-child {
        margin-right: 0;
      }
      .selector {
        display: inline-block;
        width: 100%;
        white-space: nowrap;
        padding-left: 16px;
        padding-right: 22px;
        background: url(../images/white-triangle-down.svg) 100% no-repeat;
        background-position: 100% 50%;
        img {
          width: 24px;
          height: 24px;
          vertical-align: middle;
        }
        span {
          position: relative;
          top: 1px;
          line-height: 24px;
          font-family: AvenirNext-Medium;
          font-size: 20px;
          color: #fff;
          vertical-align: middle;
          display: inline-block;
          margin: 0 0 0 6px;
        }
      }
    }
  }

  .line-type-area {
    .header {
      display: flex;
      align-items: center;
      padding: 0 40px 20px;
      .total {
        margin-right: 10px;
        font-size: 30px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: #ffffff;
        line-height: 42px;
      }
      .rate {
        font-size: 15px;
        font-family: PingFangSC-Semibold, PingFang SC;
        line-height: 21px;
      }
      .header-l {
        flex: 1;
      }
    }
  }

  .price-card {
    .header-wrap {
      padding: 0 40px 20px;
    }
    .header-top {
      display: flex;
      align-items: center;
      .header-l {
        flex: 1;
      }
    }
    .time {
      margin: 0 10px;
      font-size: 12px;
      line-height: 17px;
      color: rgba(255, 255, 255, 0.3);
    }
    .total {
      margin-right: 10px;
      font-size: 30px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: #ffffff;
      line-height: 42px;
    }
    .rate {
      font-size: 15px;
      font-family: PingFangSC-Semibold, PingFang SC;
      line-height: 21px;
    }
    .header-row {
      margin-top: 10px;
      span {
        margin-right: 3px;
        font-size: 12px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: rgba(255, 255, 255, 0.3);
        line-height: 17px;
        &:nth-child(2n) {
          margin-right: 10px;
        }
        &:last-child {
          margin-right: 0;
        }
      }
      .num {
        color: #fff;
      }
      .color-white {
        .num {
          color: #fff;
        }
      }
      .color-red2 {
        .num {
          color: #ff4f88;
        }
      }
      .color-green {
        .num {
          color: #16c378;
        }
      }
    }
  }

  .time-picker {
    display: flex;
    background: rgba(193, 160, 255, 0.5);
    color: rgba(255, 255, 255, 0.5);
    border-radius: 30px;
    overflow: hidden;
    .time-picker-item {
      width: 70px;
      padding: 2px 15px;
      font-size: 12px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      line-height: 17px;
      text-align: center;
      user-select: none;
      border-radius: 30px;
      cursor: pointer;
    }
    .time-picker-active {
      background: rgba(193, 160, 255, 1);
      color: rgba(90, 20, 225, 1);
    }
  }

  .color-red2 {
    color: #ff4f88;
  }

  .color-green {
    color: #16c378;
  }

  .color-white {
    color: #fff !important;
  }
}

.ant-tooltip-inner {
  width: fit-content;
  padding: 14px 20px;
  border-radius: 5px;
}

@include mobile-only {
  .pc {
    display: none !important;
  }
  .mobile {
    display: block !important;
  }
  .new-scan {
    .new-scan-main {
      padding: 0 10px 20px;
      max-width: 100%;
      .new-scan-section {
        margin-bottom: 20px;
      }
      .new-scan-title {
        margin-bottom: 10px;
        font-size: 15px;
        line-height: 21px;
      }
      .table-box {
        border-radius: 6px;
        .ant-tabs-nav {
          margin: 0;
          .ant-tabs-ink-bar {
            bottom: 5px;
          }
        }
        .ant-tabs-nav-wrap {
          padding: 0px 10px;
          .ant-tabs-tab {
            padding: 12px 0;
            font-size: 15px;
            line-height: 21px;
          }
          .ant-tabs-ink-bar {
            &:after {
              width: 20px;
            }
          }
        }
      }
    }
    .token-wrap {
      padding: 20px 0;
      .token-select {
        min-width: 120px;
        margin-right: 10px;
        padding-right: 7px;
        height: 38px;
        border-radius: 6px;
        &:last-child {
          margin-right: 0;
        }
        .selector {
          padding-left: 10px;
          padding-right: 12px;
          img {
            width: 22px;
            height: 22px;
            vertical-align: middle;
          }
          span {
            line-height: 22px;
            font-size: 15px;
            margin: 0 0 0 6px;
          }
        }
      }
    }
    .line-type-area {
      .header {
        display: block;
        padding: 15px 10px;
        .total {
          margin-right: 5px;
          font-size: 24px;
          line-height: 33px;
        }
        .rate {
          font-size: 12px;
          line-height: 17px;
        }
      }
    }
    .price-card {
      .header-wrap {
        padding: 15px 10px;
      }
      .total {
        margin-right: 5px;
        font-size: 24px;
        line-height: 33px;
      }
      .rate {
        font-size: 12px;
        line-height: 17px;
      }
      .timer-row {
        display: flex;
        align-items: center;
      }
      .time-picker {
        margin-top: 0;
      }
      .header-row {
        span {
          margin-right: 3px;
          font-size: 12px;
          font-family: PingFangSC-Regular, PingFang SC;
          font-weight: 400;
          color: rgba(255, 255, 255, 0.3);
          line-height: 17px;
          &:nth-child(2n) {
            margin-right: 10px;
          }
          &:last-child {
            margin-right: 0;
          }
        }
      }
    }
    .time-picker {
      margin-top: 10px;
      width: 140px;
    }
  }
}

@include desktop-only {
  .mobile {
    display: none !important;
  }
}
